@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

body {
  background: #1e1f26;
  height: 100vh;
}

input,
textarea {
  font-weight: inherit;
  font-style: inherit;
  color: inherit;
  background-color: inherit;
  border: 1px solid #ccc;
  border-color: inherit;
  border-radius: 4px;
  outline: none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

textarea {
  width: 100%;
  padding: 10px 10px;
  resize: vertical;
}

input {
  max-width: 200px;
  padding: 2px 10px;
}

.container {
  background: white;
  width: 860px;
  min-height: 840px;
  height: 100%;
  margin: auto;
  padding: 0 60px 60px 60px;
}

.dark {
  background: black;
  color: white;
  border-color: white;
}

.header__options {
  display: flex;
  justify-content: flex-end;
  padding: 20px 0;
  height: 70px;
  opacity: 0;
  transition: 0.2s ease;
}

.header:hover .header__options {
  opacity: 1;
}

.btn {
  cursor: pointer;
  border: 0;
  background: none;
  opacity: 0.6;
}

.hide {
  display: none;
}

.btn:hover {
  opacity: 1;
}

.btn--edit {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 1.2rem;
  border-radius: 5px;
}

.btn--delete {
  position: absolute;
  left: 140px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 5px;
  font-size: 1.2rem;
}

.btn--add {
  margin-left: 180px;
  padding: 9px 19px;
  font-weight: 500;
  background: white;
  border-radius: 5px;
  border: 1.5px solid black;
}

.btn--save {
  position: absolute;
  right: 0;
  top: 0;
  padding: 9px 19px;
  background: white;
  border-radius: 5px;
  border: 1.5px solid black;
}

.header {
  margin-bottom: 40px;
}

.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.toggle__title {
  padding-right: 10px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 26px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 18px;
  width: 18px;
  left: 6px;
  bottom: 4px;
  border-radius: 50%;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #494e52;
}

input:focus + .slider {
  box-shadow: 0 0 1px transparent;
}

input:checked + .slider:before {
  transform: translateX(22px);
}

.header__general-info {
  position: relative;
}

.header__name {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.header__introduction {
  padding-right: 100px;
  margin-bottom: 10px;
  line-height: 1.7rem;
}

.header__introduction--text {
  margin-bottom: 10px;
  width: 80%;
}

.header__contact-details {
  font-size: 0.9rem;
  line-height: 1.5rem;
  font-weight: bold;
  display: flex;
}

.header__contact-details p {
  margin-right: 40px;
}

.header__contact-details input {
  margin-right: 40px;
  padding: 5px 10px;
}

.section {
  position: relative;
  margin-bottom: 40px;
}

.section__title {
  color: grey;
  position: absolute;
  top: 0;
  left: 0;
}

.section__item {
  position: relative;
  padding-left: 180px;
  margin-bottom: 30px;
}

.section__info {
  line-height: 1.7rem;
  margin-bottom: 20px;
  position: relative;
}

.primary {
  font-size: 1rem;
  font-weight: bold;
}

.secondary {
  font-size: 1rem;
  font-weight: bold;
  color: grey;
}

.date {
  color: grey;
  margin: 6px 0 6px 0;
}

.skills-text {
  width: 80%;
}

